
.contactpage {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  padding: 15vh 0 15vh 0;
  width: 100vw;
  height: 93vh;
}

.media {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  padding: 0 2vw 0 2vw;
  width: 60vw;
  height: 15vh;
  background-color: #EAEAEA;
  border-radius: 70px;
  margin: auto;
  cursor: pointer;
  transition: all 100ms ease-in-out;
}
.media:hover {
  filter: brightness(0.9);
}
.media img {
  height: 70%;
}
.media .name {
  font-family: "Readex Pro";
  color: #636363;
  font-size: 1.5rem;
  margin: auto 0 auto 1vw;
}
.media .prob {
  margin: auto 0.5vw auto auto;
  font-size: 1.5rem;
  font-family: "Readex Pro";
  color: #636363;
}

