.MainContent {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100vw;
  height: 93vh;
}
.MainContent .tittle {
  font-family: "Readex Pro";
  text-align: center;
  font-size: 4rem;
  color: #474747;
  margin: auto 0 0 0;
}
.MainContent .description {
  font-family: "Readex Pro";
  font-size: 1.2rem;
  text-align: center;
  color: #B0B0B0;
  font-weight: normal;
  margin: 0;
}
.MainContent .btns {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30vh;
  width: 60vw;
  margin: 50px 0 auto 0;
}
.MainContent .btns .btn {
  width: 10vw;
  height: 10vw;
  border-radius: 20px;
  background-color: #EAEAEA;
  margin: 0 auto;
  user-select: none;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: all 100ms ease-in-out;
  text-decoration: none;
}
.MainContent .btns .btn p {
  margin: 0 0 20% 0;
  font-family: "Readex Pro";
  font-size: 1rem;
  color: #636363;
}
.MainContent .btns .btn img {
  width: 40%;
}
.MainContent .btns .btn:hover {
  filter: brightness(0.9);
}

