.bookmarkspage {
  width: 100vw;
  height: 93vh;
  box-sizing: border-box;
  padding: 20px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 20vh);
  grid-column-gap: 20px;
  grid-row-gap: 20px;
}

.bkm {
  box-sizing: border-box;
  padding: 20px;
  margin: 0;
  width: 100%;
  height: 100%;
  border-radius: 20px;
  max-width: 31.5vw;
}
.bkm .inside {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  padding: 20px;
  width: 100%;
  height: 100%;
  background-color: #EAEAEA;
  border-radius: 20px;
  box-sizing: border-box;
  transition: all 100ms ease-in-out;
  cursor: pointer;
}
.bkm .inside:hover {
  filter: brightness(0.9);
}
.bkm .inside img {
  height: 90%;
  margin: auto 0;
}
.bkm .inside .title {
  font-family: "Readex Pro";
  color: #636363;
  font-size: 1.5rem;
  margin-left: 10px;
}
