
.sidebar {
  display: flex;
  flex-direction: column;
  height: 93vh;
  width: 20vw;
  background-color: #eaeaea;
}
.sidebar .presentation {
  display: flex;
  align-items: center;
  width: 100%;
  height: 8vh;
  box-sizing: border-box;
  padding: 0 1vw;
}
.sidebar .presentation .logo {
  height: 70%;
}
.sidebar .presentation p {
  font-family: "Readex Pro";
  color: #636363;
  font-size: 1.2rem;
  margin-left: 0.5vw;
}
.sidebar .presentation .down {
  height: 0.9vw;
  margin: auto 15px auto auto;
  cursor: pointer;
  transition: all 100ms ease-out;
}
.sidebar .presentation .down:hover {
  transform: scale(1.2);
}
.sidebar .presentation .link {
  cursor: pointer;
  width: 1.2vw;
  margin: auto 10px auto 0;
  transition: all 100ms ease-out;
}
.sidebar .presentation .link:hover {
  transform: scale(1.2);
}
.sidebar .presentation .link.auto-left {
  margin: auto 10px auto auto;
}
.sidebar .moreinfo {
  padding: 0 1vw;
  box-sizing: border-box;
  height: max-content;
  width: 100%;
}
.sidebar .moreinfo p {
  margin-top: 0;
  font-family: "Readex Pro";
  color: #636363;
  font-size: 0.8rem;
}
.sidebar .separator {
  width: 90%;
  height: 1px;
  margin: 0 auto;
  background-color: #B0B0B0;
}
.sidebar .links {
  padding: 5px 0.7vw;
  box-sizing: border-box;
}
.sidebar .links .link {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  padding: 0 0.3vw;
  height: 5vh;
  font-family: "Readex Pro";
  color: #636363;
  width: 100%;
  border-radius: 7px;
  cursor: pointer;
}
.sidebar .links .link:hover {
  background-color: #D9D9D9;
}

