

body {
    margin: 0;
    padding: 0;
}

.NavBar {
    display: flex;
    align-items: center;
    width: 100vw;
    height: 7vh;
    background-color: #EAEAEA;

    
  }
  .NavBar .logo {
    height: 80%;
    margin-left: 1vw;
    cursor: pointer;
    transition: all 100ms ease-in-out;
  }
  .NavBar .logo:hover {
    filter: brightness(1.1);
  }

  .NavBar .uri {
    color: #b0b0b0;
    font-family: "Readex Pro";
    margin-left: 0.35vw;
    font-size: 1.1rem;
  }
  