
.selectapp {
  display: flex;
  flex-direction: column;
}
.selectapp div {
  width: 100%;
  box-sizing: border-box;
  padding: 0 1.5vw;
  margin-bottom: 1vh;
}
.selectapp div .title {
  color: #636363;
  font-family: "Readex Pro";
  font-size: 1.1rem;
}
.selectapp div .apps {
  display: flex;
  align-items: center;
  justify-content: left;
  padding: 0;
}
.selectapp div .apps .app {
  user-select: none;
  padding: 0;
  display: flex;
  flex-direction: column;
  width: 8vw;
  height: 8vw;
  background-color: #EAEAEA;
  border-radius: 5px;
  transition: all 100ms ease-in-out;
  cursor: pointer;
  margin: 0 1vw 0 0;
}
.selectapp div .apps .app .title {
  margin: 10px auto 0 auto;
  text-align: center;
}
.selectapp div .apps .app:hover {
  background-color: #D9D9D9;
}
.selectapp div .apps .app img {
  padding: 0;
  margin: 0;
  margin: auto;
  height: 70%;
}

